import 'es6-promise/auto'
import Vue from 'vue'
import Axios from 'axios'
import Common from './components/common.js';

Vue.component('common', Common);

new Vue({
    el: '#scope-board-view',
    data: function() {
        return {
            bbsContentViewModel: {
                detailContent: {bbs_id:''},
                isUpdate:false
            }
        }
    },
    mounted: function() {
        console.log("mounted [board view]");
        try {
            pageLoad(this);
        } catch (e) {
            console.log(e);
        }
    },
    methods: {
        fnGetInit: function(paramBbsID,paramSeq,parmaLogId){
            let vm = this;
            if(paramBbsID==null)
                paramBbsID = vm.bbsContentViewModel.bbsId;
            Axios.post("/api/admin/board/content/detail", {id:paramBbsID,paramSeq:paramSeq})
            .then(function(response) {
                let bbsNm = paramBbsID=='BBS_001'?'notice':'general';
                vm.bbsContentViewModel.bbsTitle=paramBbsID=='BBS_001'?'공지':'자유';
                let rs = response.data;
                vm.bbsContentViewModel.detailContent = rs.detailContent;
                vm.bbsContentViewModel.detailContent.create_date = Common.fnConvertDateTimeStr(vm.bbsContentViewModel.detailContent.create_date,'-');
                vm.bbsContentViewModel.detailContent.updateUrl = "/board/"+bbsNm+"/update/"+vm.bbsContentViewModel.detailContent.bbs_id+"/"+vm.bbsContentViewModel.detailContent.bbs_seq;
                vm.bbsContentViewModel.detailContent.listUrl = "/board/"+bbsNm;
                if(parmaLogId==vm.bbsContentViewModel.detailContent.create_user_id){
                    vm.bbsContentViewModel.isUpdate=true;
                }else{
                    vm.bbsContentViewModel.isUpdate=false;
                }
            }).catch(function(ex) {
                console.log(ex);
            });
        }
        ,fnFileDownload: function(paramSeq){
            Common.fnOnFileDownload(paramSeq);
        }        
    }
});


